<template>
  <u-popup v-model="localShow" mode="center" border-radius="20"
    :custom-style="{ backgroundColor: 'rgba(0, 0, 0, 0.7)' }">
    <view class="bullet-recharge-box">
      <view class="bullet-recharge-header">
        <u-icon class="close-icon" name="close" color="#333" size="38" @click="close"></u-icon>
        <view class="bullet-recharge-title">子弹充值</view>
      </view>
      <view class="bullet-recharge-content">
        <view class="bullet-count">
          <text>当前子弹</text>
          <text class="count-num">{{ localCoin }}</text>
        </view>
        <view class="recharge-options">
          <view class="recharge-item" v-for="(item, index) in coinList" :key="index"
            :class="{ active: selectedRecharge.id == item.id }" @click="selectRecharge(item)">
            <view class="recharge-amount">{{ item.coin }}发</view>
            <view class="recharge-price">￥{{ item.rmb }}</view>
            <view class="recharge-tag" v-if="item.tag">{{ item.tag }}</view>
          </view>
        </view>
      </view>
      <view class="bullet-recharge-footer">
        <button class="cancel-btn" @click="close">取消</button>
        <button class="recharge-btn" @click="confirmRecharge">立即充值</button>
      </view>
    </view>
  </u-popup>
</template>

<script>
export default {
  name: 'BulletRechargeDialog',
  props: {
    localCoin: {
      type: [String,Number]
    },
    show: {
      type: Boolean,
      default: false
    },
    userInfo: {
      type: Object,
      default: () => ({})
    },
    coinList: {
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
      localShow: this.show,
      selectedRecharge: {
        id:null
      }
    }
  },
  watch: {
    show (val) {
      this.localShow = val
    },
    localShow (val) {
      this.$emit('update:show', val)
    }
  },
  methods: {
    close () {
      this.localShow = false
    },
    selectRecharge (item) {
      this.selectedRecharge = item
    },
    confirmRecharge () {
      if (!this.selectedRecharge.id) {
        uni.showToast({
          title: '请选择充值数量',
          icon: 'none'
        })
        return
      }
      this.$emit('confirm', this.selectedRecharge)
    }
  }
}
</script>


<style lang="scss" scoped>
.bullet-recharge-box {
  width: 650rpx;
  background: linear-gradient(180deg, #FFFFFF 0%, #F8F8F8 100%);
  border-radius: 30rpx;
  padding: 40rpx;
  position: relative;

  .bullet-recharge-header {
    position: relative;
    text-align: center;
    margin-bottom: 40rpx;

    .close-icon {
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 40rpx;
      height: 40rpx;
    }

    .bullet-recharge-title {
      font-size: 36rpx;
      font-weight: bold;
      color: #333;
      background: linear-gradient(90deg, #FF6B6B 0%, #FF8E8E 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }

  .bullet-recharge-content {
    .bullet-count {
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      font-size: 28rpx;
      margin-bottom: 40rpx;
      background: rgba(0, 0, 0, 0.5);
      border-radius: 20rpx;
      color: #fff;

      .count-num {
        font-size: 48rpx;
        font-weight: bold;
        color: #FF6B6B;
        margin-left: 20rpx;
      }
    }

    .recharge-options {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 20rpx;

      .recharge-item {
        background: #FFFFFF;
        border-radius: 16rpx;
        padding: 20rpx;
        text-align: center;
        position: relative;
        box-shadow: 0 4rpx 12rpx rgba(0, 0, 0, 0.05);
        transition: all 0.3s ease;

        &.active {
          background: #FFF0F0;
          border: 2rpx solid #FF6B6B;
          transform: scale(1.05);
        }

        .recharge-amount {
          font-size: 32rpx;
          font-weight: bold;
          color: #333;
          margin-bottom: 10rpx;
        }

        .recharge-price {
          font-size: 28rpx;
          color: #FF6B6B;
          font-weight: bold;
        }

        .recharge-tag {
          position: absolute;
          top: -10rpx;
          right: -10rpx;
          background: #FF6B6B;
          color: #fff;
          font-size: 20rpx;
          padding: 4rpx 12rpx;
          border-radius: 20rpx;
        }
      }
    }
  }

  .bullet-recharge-footer {
    margin-top: 40rpx;
    display: flex;
    justify-content: space-between;
    gap: 20rpx;

    button {
      flex: 1;
      height: 88rpx;
      line-height: 88rpx;
      text-align: center;
      border-radius: 44rpx;
      font-size: 32rpx;
      font-weight: bold;

      &.recharge-btn {
        background: linear-gradient(90deg, #FF6B6B 0%, #FF8E8E 100%);
        color: #fff;
        box-shadow: 0 8rpx 16rpx rgba(255, 107, 107, 0.3);
      }

      &.cancel-btn {
        background: #F5F5F5;
        color: #666;
      }
    }
  }
}
</style>
